.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 100;
}

.modal {
    position: fixed;
    top: 40%;
    left: 50%;
    max-height: 100vh;
    transform: translate(-50%, -50%);
    z-index: 100;
}
